'use client'

import React, { Suspense } from 'react'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import {
  getTrendingAnimeGogo,
  getPopularAnimeGogo,
} from '@/lib/api-gogo' // ganti lib sesuai file Gogoanime

import {
  HeroSkeleton,
  StaticSkeletonGrid,
  FlexSkeletonGrid,
} from '@/components/skeleton-loader'

// ========================
// Sections
// ========================

async function HeroSection() {
  const data = await getTrendingAnimeGogo()
  const animes = data.slice(0, 5).map((item) => ({
    mal_id: Number(item.animeId), // konversi ke number
    title: item.title,
    images: {
      jpg: {
        image_url: item.image,
        small_image_url: item.image,
        large_image_url: item.image,
      },
    },
  }))

  const { HeroCarousel } = await import('@/components/hero-carousel')
  return (
    <div className="mt-8 mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <HeroCarousel animes={animes} />
    </div>
  )
}

async function TopSection() {
  const data = await getTrendingAnimeGogo()
  const animes = data.slice(0, 5).map((item) => ({
    mal_id: Number(item.animeId),
    title: item.title,
    images: {
      jpg: {
        image_url: item.image,
        small_image_url: item.image,
        large_image_url: item.image,
      },
    },
  }))

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-xl sm:text-2xl font-bold text-foreground mb-3 sm:mb-4">
        Top Anime
      </h2>
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-5 gap-3 sm:gap-4 md:gap-6">
        {animes.map((anime, index) => (
          <AnimeCard key={`${anime.mal_id}-${index}`} anime={anime} />
        ))}
      </div>
    </div>
  )
}

